@import '../../assets/scss/color';
@import '../../assets/scss/fonts';


















































































































































































































































































































































































.el-table th,
.el-table tr {
  background-color: $color-back-card;
  transition: background-color 0.8s;
  height: 32px;
}
.el-table th.gutter {
  display: table-cell !important;
}
.el-table--small td,
.el-table--small th {
  padding: 0;
}
// 滚动条样式
.standardTable {
  > .el-table {
    border-bottom: 1px solid $color-table-cutting;
    border-left: 1px solid $color-table-cutting;
    border-right: 1px solid $color-table-cutting;
    //  padding: 0 10px;
  }
  .borderStyle {
    border-bottom: 1px solid $color-table-cutting;
    border-right: 1px solid $color-table-cutting;
  }
  .el-table th.is-leaf,
  .el-table td,
  .el-table--group,
  .el-table--border {
    border-color: $color-table-cutting;
  }
  .el-table th > .cell {
    color: rgba(#3c4455, 0.7);
    font-weight: 600;
  }
  .el-table--border th,
  .el-table--border td {
    border-color: $color-table-cutting;
  }

  > .el-table.el-table--small {
    color: $color-main-text;

    &.el-table--striped .el-table__body tr.el-table__row--striped td {
      background: $stripe;
    }
    .el-table__header-wrapper {
      th {
        &.is-leaf {
          border-bottom: 1px solid $color-table-cutting;
        }
        background-color: $header-color;
        // padding: 8px 0;
        color: $text-color-help;
        font-size: $font-size-base;
      }
    }
    .el-table__body-wrapper {
      // 表格行高
      td .cell {
        line-height: 20px; // font-size: 14px;
      }
    }
    .el-table__fixed {
      position: absolute;
      top: 0;
      left: 0;
      overflow-x: hidden;
      overflow-y: hidden;
      // box-shadow: 0 -10px 10px rgba(0, 0, 0, 0.12);
    }
    .el-table.el-table--small .el-table__fixed,
    .el-table.el-table--small .el-table__fixed-right {
      box-shadow: 0 !important;
    }
    .el-table__body-wrapper.is-scrolling-left ~ .el-table__fixed {
      box-shadow: none !important;
    }
    .el-table__fixed,
    .el-table__fixed-right {
      // box-shadow: 0 -10px 10px rgba(0, 0, 0, 0.12);
      .el-table__fixed-header-wrapper {
        th {
          &.is-leaf {
            border-bottom: none;
          }
          // background-color: $header-color;
          // padding: 8px 0;
          // color: $text-color-help;
          // font-size: $font-size-base;
        }
      }
      .el-table__fixed-body-wrapper {
        // 表格行高
        td .cell {
          line-height: 20px; // font-size: 14px;
        }
      }
    }
    .el-table__fixed::before,
    .el-table__fixed-right::before {
      height: 0px;
    }
  }

  .el-table--enable-row-hover .el-table__body tr:hover > td {
    background-color: $color-form-hover;
  }

  .el-table__expand-icon {
    .el-icon-arrow-right::before {
      color: #b5b5b5;
      content: '\e791';
    }
  }

  .el-table--scrollable-y {
    .el-table__fixed-right {
      right: 10px !important;
    }
    .el-table__fixed-right-patch {
      width: 10px !important;
    }
  }

  .el-table__empty-text {
    left: 50%;
  }
}

/*滚动条整体部分,必须要设置*/
::-webkit-scrollbar {
  width: 10px;
  height: 8px;
  background-color: $color-control;
}
/*滚动条的轨道*/
::-webkit-scrollbar-track {
  background-color: $color-control;
}
/*滚动条的滑块按钮*/
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: $color-icon;
}
/*滚动条的上下两端的按钮*/
::-webkit-scrollbar-button {
  height: 10px;
  background-color: $color-control;
}

.el-table .cell {
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  word-break: break-all;
  font-family: MicrosoftYaHei;
  line-height: 20px;
  font-weight: normal;
  padding-right: 10px;
}
// .base-table .el-table--border, .base-table .el-table--group {
//   border: 1px solid $color-table-cutting !important;
// }
.base-table {
  background-color: $color-back-card;
  .table-wrap {
    .el-table__row td {
      border-color: $color-table-cutting;
      padding: 0;
      line-height: 20px;
      font-size: 14px;
      .el-button {
        padding: 0;
      }
    }
    .el-button--text {
      border-color: transparent;
      font-size: 14px;
      line-height: 20px;
      background: transparent;
    }
    .table {
      .sort-caret.descending {
        border-top-color: #fff;
      }
      .sort-caret.ascending {
        border-bottom-color: #fff;
      }
      .ascending {
        .sort-caret.ascending {
          border-bottom-color: #de680b;
        }
      }
      .descending {
        .sort-caret.descending {
          border-top-color: #de680b;
        }
      }
      .el-table-column--selection .cell {
        overflow: visible !important;
      }
      .el-table__body,
      .el-table__footer,
      .el-table__header {
        border-left: 1px solid $color-table-cutting;
      }
      .table_head {
        th {
          align-self: center;
          color: $color-header;
          border-top: 1px solid $color-table-cutting;
          border-color: $color-table-cutting;
          background-color: $color-back-header;
          transition: background-color 0.8s;
          .el-checkbox__input.is-indeterminate,
          .el-checkbox__input.is-checked,
          .el-checkbox__input.is-focus {
            .el-checkbox__inner {
              background-color: #fff;
              border-color: $color-table-cutting;
            }
          }
          .el-checkbox__inner::before {
            background-color: $color-main;
          }
          .el-checkbox__inner::after {
            border-color: $color-main;
          }

          .cell {
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            padding: 9px 10px;
            font-family: MicrosoftYaHei;
            font-size: 14px;
            color: $color-subtitle-text;
            line-height: 20px;
            font-weight: normal;
            .thead-cell {
              display: inline-flex;
              flex-direction: row;
              align-items: left;
              padding: 0;
              overflow: initial;
              cursor: pointer;
            }
          }
          .el-table__fixed {
            cursor: not-allowed;
          }
        }
        .el-checkbox.is-checked {
          color: $color-background;
        }
      }
      .el-table__footer {
        &::after {
          position: absolute;
          bottom: 0;
          left: 0;
          display: block;
          width: 100%;
          height: 2px;
          content: '';
          background-color: rgba($color-mainfont, 0.4);
        }
        tbody td {
          height: 36px;
          padding: 0;
          font-size: 14px;
          font-weight: bold;
          line-height: 36px;
          color: $color-mainfont;
          background-color: $color-body-bg;
          border-right-color: $color-body-bg;
        }
      }
    }
    .table .el-table__footer-wrapper .el-table__footer {
      &::before {
        position: absolute;
        left: 0;
        z-index: 4;
        display: block;
        width: 100%;
        height: 2px;
        content: '';
        background-color: rgba($color-mainfont, 0.4);
      }
    }
    .base-pagination {
      font-size: 12px;
      color: $color-subtitle-text;
      line-height: 20px;
      height: 20px;
      padding-top: 10px;
      text-align: right;
      background-color: $color-back-card;
      transition: background-color 0.8s;
      .el-pagination__total,
      .el-pagination__sizes,
      .el-pagination__jump {
        font-size: 12px;
        line-height: 20px;
      }
      .el-pagination__total,
      .el-pagination__sizes {
        float: left;
      }
      .btn-prev,
      .btn-next,
      .el-pager li {
        margin: 0px 5px;
        font-size: 12px;
        height: 20px;
        line-height: 20px;
      }

      .el-pager li {
        font-weight: 400;
        color: $color-main-text;
        margin: 0 5px;
        min-width: 20px;
        height: 20px;
        line-height: 20px;
        border-radius: 2px;
      }
      .el-pager li:not(.disabled).active {
        background-color: $color-main;
        color: #ffffff;
      }
      .el-input .el-input__inner {
        height: 20px;
        line-height: 20px;
      }
      .el-input--mini .el-input__icon {
        line-height: 20px;
      }
    }
  }
  .el-table--border,
  .el-table--group {
    border: none;
  }
  .el-table__fixed::before,
  .el-table__fixed-right::before,
  .el-table::before,
  .el-table--group::after,
  .el-table--border::after {
    width: 0;
  }
  // .summary {
  //   .borderStyle {
  //     border-bottom: 1px solid rgba($color-mainfont, 0.2);
  //   }
  // }
  .table .el-table__fixed-header-wrapper {
    .table_head th:last-child {
      border-right: none;
    }
  }
  .el-table {
    .el-table__body {
      tr.el-table__row--striped > td {
        background-color: $color-background;
        border-right-color: $color-background;
      }
      tr.hover-row > td {
        background-color: $color-form-hover;
        border-right-color: $color-form-hover;
      }
    }

    .el-table__fixed-right-patch {
      background-color: $color-main;
      border-bottom-color: $color-main;
    }
  }
}
.pd20 {
  padding-bottom: 20px;
}
.polymerization-table {
  .base-table {
    .table-wrap {
      .table {
        .table_head {
          th {
            color: $color-mainfont;
            background-color: rgba($color-main, 0.2);
          }
        }
      }
    }
  }
}

.authorizeSpan:last-child {
  display: none;
}
.pageNumber {
  padding: 6px 0 16px 10px;
  margin: 0;
  font-size: 14px;
  color: $color-mainfont;
  background-color: $color-white;
}
.el-tooltip__popper {
  max-width: 350px;
}
