@import '../../assets/scss/color';
@import '../../assets/scss/fonts';


















































































































































































.el-select-dropdown.is-multiple .el-select-dropdown__item.selected::after {
  content: '' !important;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected .check {
  background-color: #409eff;
  border-color: #409eff;
}
.card_select_foot {
  border-top: 1px solid $color-table-cutting;
  background-color: $color-back-card;
}
.el-select-dropdown.is-multiple
  .el-select-dropdown__item.selected
  .check:after {
  transform: rotate(45deg) scaleY(1);
}
.el-select-dropdown.is-multiple .el-select-dropdown__item .check::after {
  box-sizing: content-box;
  content: '';
  border: 1px solid #fff;
  border-left: 0;
  border-top: 0;
  height: 7px;
  left: 4px;
  position: absolute;
  top: 1px;
  transform: rotate(45deg) scaleY(0);
  width: 3px;
  transition: transform 0.15s ease-in 0.05s;
  transform-origin: center;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item .check {
  display: inline-block;
  position: relative;
  top: 2px;
  border: 1px solid #dcdfe6;
  border-radius: 2px;
  box-sizing: border-box;
  width: 14px;
  height: 14px;
  background-color: #fff;
  z-index: 1;
  transition: border-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46),
    background-color 0.25s cubic-bezier(0.71, -0.46, 0.29, 1.46);
}
.node-card {
  .el-cascader-panel {
    .el-scrollbar__wrap {
      height: 240px !important;
    }
    .el-cascader-menu__list {
      height: 200px !important;
      overflow-y: scroll !important;
    }
  }
  // .select-checkbox {
  // }
  background: $color-back-card;
  position: relative;
  border: 1px solid $color-control;
  border-radius: 2px 2px 0 0;

  &-title {
    position: relative;
    height: 40px;
    line-height: 40px;
    background: $color-back-header;
    border-bottom: 1px solid $color-table-cutting;

    &-left {
      float: left;
      height: 100%;
      padding-left: 19px;
      color: $color-subtitle-text;
      font-size: 14px;
      font-weight: 500;
    }

    &-btns {
      float: right;
      font-size: 12px;
      color: #bfc3cc;
      padding-right: 10px;

      .el-button {
        .icon {
          margin-right: 4px;
        }
      }
    }
  }

  &-body {
    position: relative;
  }
}

.card-shadow {
  box-shadow: 0px 0px 8px rgba(#0f1e42, 0.1);
}
.card-title-background {
  background: $color-back-header !important;
}
