@import '../../assets/scss/color';
@import '../../assets/scss/fonts';



































































































.tags-view-container {
  background-color: $color-back-card;
  transition: background-color 0.8s;
  // box-shadow: inset 0 -1px 0 0 $color-line-nav;
  .tags-view-item {
    position: relative;
    display: inline-block;
    height: 30px;
    padding: 0 10px;
    margin-left: 10px;
    font-size: 12px;
    line-height: 30px;
    color: $color-main-text;
    // background: $color-main;
    // border: 1px solid $color-body-bg;
    border-bottom: none;
    border-radius: 2px 2px 0 0;
    &:first-of-type {
      margin-left: 20px;
    }
    &:last-of-type {
      margin-right: 20px;
    }
    &.router-link-exact-active {
      color: $color-subtitle-text;
      font-weight: bold;
      background-color: $color-back-page;
      border-color: $color-back-page;
      transition: background-color 0.8s;
    }
    .icon {
      margin-left: 6px;
      font-size: 15px;
      &:hover {
        background: rgba($color-mainfont, 0.2);
        border-radius: 50%;
      }
    }
  }
  .draging {
    top: 0;
    left: 0;
  }
}
