@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';















































.el-menu--collapse {
  .el-submenu__title > span,
  .el-submenu__title > i {
    display: none;
  }
}
.el-menu.v-leave-to {
  .el-submenu__title > span,
  .el-submenu__title > i,
  .is-opened {
    display: none;
  }
}
.el-menu {
  border: none;
  background-color: $color-back-header;
}
.navmenu_out .el-menu {
  background-color: $color-back-click;
}
.is-opened {
  .is-active {
    background-color: #1c3b82;
  }
  overflow: hidden;
}

.el-menu.el-menu--horizontal {
  background-color: $color-main;
  border-bottom: none;
  .el-menu-item {
    float: left;
    height: 46px;
    padding: 0 15px;
    line-height: 46px;
    color: $color-white;
    &:focus,
    &:hover,
    &.is-active {
      color: $color-white !important;
      background: $color-table-header-bg !important;
    }
  }
  .el-submenu {
    float: left;
    .el-submenu__title {
      height: 46px;
      padding: 0 15px;
      line-height: 46px;
      color: $color-white;
      > i {
        display: none;
      }
      &:focus,
      &:hover {
        // font-weight: bold;
        color: $color-white;
        background: $color-table-header-bg !important;
        // border-bottom: 3px solid $color-main-hover !important;
      }
    }
    &.is-active {
      .el-submenu__title {
        // font-weight: bold;
        color: $color-white !important;
        background: $color-table-header-bg !important;
        // border-bottom: 3px solid $color-main-hover !important;
      }
    }
  }
}
.el-menu.el-menu--horizontal:link {
  // border-color: red;
  // box-shadow: none;
  background-color: red;
}
