@import '../../assets/scss/color';
@import '../../assets/scss/fonts';


















































































.base-panel {
  padding: 0 20px 20px;
  background: $color-back-card;
  transition: background-color 0.8s;
  border-radius: 2px;
  position: relative;
  box-shadow: 0px 0px 8px rgba(#0f1e42, 0.1);

  &-title {
    position: relative;
    height: 50px;
    line-height: 50px;
    // border-bottom: 1px solid #dbdcdc;
    // margin-bottom: 10px;

    &-left {
      float: left;
      height: 100%;
      > span {
        position: relative;
        padding-left: 14px;
        color: $color-subtitle-text;
        font-size: 14px;
      }
    }
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 0;
      top: 20px;
      /*height: 30px;*/
      width: 4px;
      height: 12px;
      background-color: $color-main;
    }
    &-right {
      float: right;
    }
  }

  .base-panel-title-right {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 9;
    .el-button {
      padding: 7px 15px;
      // color: red;
      // border-color: #d8dbe6;
      border-radius: 2px;

      .icon {
        margin-right: 4px;
      }
    }
    .el-button--primary {
      background-color: #3674f9;
      border-color: #3674f9;
      color: #fff;
    }
  }
  &-body {
    .operate-region {
      float: right;
      margin: 0 20px;

      .defaultBtn {
        margin-bottom: 10px;
      }
    }
  }

  .actionIcon {
    width: 16px;
    height: 16px;
    cursor: pointer;
  }
  //  右边下拉菜单的样式
  .dropDownMenu {
    line-height: 30px;
  }
  //  右边日期选择框
  .dateBox {
    line-height: 30px;
  }
  //  左边按钮（刷新按钮有提示红点的）
  .refreshBtn {
    line-height: 12px;
  }
}

.panelTab {
  .panel-title {
    position: relative;
    border-bottom: 0px;
    margin-bottom: 0px;
    margin-bottom: -29px;
  }
  .el-tabs--card > .el-tabs__header .el-tabs__item.is-active {
    border-bottom-color: #ffffff;
    color: #3674f9;
  }
  .el-tabs__nav {
    margin-left: 40px;
  }
  .el-tabs__header {
    margin: 0px;
    .el-tabs__item {
      height: 28px;
      line-height: 28px;
      padding: 0 15px;
      color: #4b556a;
      border-color: $color-table-cutting;
    }
  }
}
