@import 'color';
@import 'fonts';

//默认样式
.formCss {
    .el-form-item {
      margin-bottom: 20px;
    }
    .el-form-item__label {
      font-size: 14px;
      color: $color-subtitle-text;
    }
    .el-input {
      width: 300px;
      input {
        height: 28px;
      }
    }
  }
  // 超长文案换行显示
  .longText .el-form-item__label {
    line-height: 16px;
    padding-right: 5px !important;
  }
  // 输入框后面跟着文案
  .addText {
    margin-left: 10px;
  }
  // 冒号处理
  .labelSuffix .el-form-item__label {
    padding: 0;
  }
  // 表单横向处理  自适应
  .formHorizontal {
    display: inline-block;
    width: 100%;
    .el-form-item {
      width: 25%; //一行4个
      margin-right: 0;
      float: left;
      .el-input,
      .el-select {
        width: 290px;
      }
    }
  }
  // 横向表单
  .formDataHorizontal {
    display: inline-block;
    width: 100%;
    .el-form-item {
      width: 50%; //一行2个
      margin-right: 0;
      float: left;
      .el-input,
      .el-select {
        width: 290px;
      }
    }
  }
  // 按钮样式 白底蓝字 14px
.btnStyle{
  border: 1px solid #E1E4EB;
  padding: 4px 20px;
  font-size: 14px;
  background: #fff;
  border-radius: 2px;
  color: #3674F9;
}
// 按钮 白底蓝字 12px
.btnIconStyle{
  border: 1px solid #E1E4EB;
  padding: 5px 10px;
  font-size: 12px;
  background: #fff;
  border-radius: 2px;
  color: #3674F9;
}
// 小标题 (前面带蓝色竖线的)
.secondTitle {
  position: relative;
  padding-left: 10px;
  color: $color-subtitle-text;
  font-size: 14px;
  &::before {
    content: '';
    display: block;
    position: absolute;
    left: 0;
    top: 3px;
    width: 5px;
    height: 16px;
    background-color: #3674F9;
  }
}

