@import '../../assets/scss/color';
@import '../../assets/scss/fonts';






























































































































































































































































































































































.navBar {
  .navBarRow {
    height: 46px;
    line-height: 46px;
    .theme_box {
      display: inline-block;
    }
  }
  .form {
    margin: 0 50px 0 35px;
  }
  .NavBar {
    float: left;
  }
  .userImg {
    float: right;
    padding-top: 10px;
    line-height: 30px;
    .headImg {
      float: left;
      width: 25px;
      height: 25px;
      background: $color-table-header-bg;
      border-radius: 50%;
    }
    .avatar {
      width: 25px;
      height: 25px;
      border-radius: 50%;
    }
    .fullScreenBtn {
      margin-right: 10px;
      font-size: 14px;
      line-height: 10px;
      color: $color-white;
      cursor: pointer;
    }
  }
  .userName {
    float: right;
    padding-left: 12px;
    margin: 0;
    font-size: 14px;
    color: $color-main-text;
    cursor: pointer;
  }
}
