@import '../../assets/scss/color';
@import '../../assets/scss/fonts';

















































.simple-card {
  background: $color-back-card;
  position: relative;
  margin-bottom: 10px;

  &-title {
    position: relative;
    height: 20px;
    line-height: 20px;
    margin-bottom: 10px;

    &-left {
      float: left;
      height: 100%;
      > span {
        position: relative;
        padding-left: 20px;
        color: $color-subtitle-text;
        font-size: 14px; // 修改依赖于禅道bug276695
        font-weight: 500;
      }
    }
    &::before {
      content: '';
      display: block;
      position: absolute;
      left: 10px;
      top: 6px;
      width: 4px;
      height: 10px;
      background-color: $color-main;
    }
    &-btns {
      float: right;

      .el-button {
        padding: 4px 10px;
        font-size: 12px;
        color: #41588e;
        border-color: #d8dbe6;
        border-radius: 2px;

        .icon {
          margin-right: 4px;
        }
      }
      .el-button--primary {
        color: #fff;
        background: #3674f9;
      }
    }
  }

  &-body {
    position: relative;
  }
}
