@import '../../assets/scss/color';
@import '../../assets/scss/fonts';

































































































































.el-aside .side-bar {
  overflow: auto;
}
/*滚动条整体样式*/
.el-aside .side-bar::-webkit-scrollbar {
  width: 0px;
  height: 1px;
}
.el-aside .side-bar::-webkit-scrollbar-thumb {
  border-radius: 1px;
  -webkit-box-shadow: inset 0 0 5px rgb(22, 42, 90);
  background: rgb(22, 42, 90);
}
/*滚动条里面轨道*/
.el-aside .side-bar::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 5px rgb(22, 42, 90);
  border-radius: 1px;
  background: rgb(22, 42, 90);
}
.layout {
  height: 100vh;
}
.page {
  height: 100%;
}

.header {
  // background: $color-back-card;
  transition: background-color 0.8s;
  border-bottom: 1px solid $color-table-cutting;
}
.conentWrap {
  height: calc(100% - 65px);
  background-color: $color-back-page;
  transition: background-color 0.8s;
}
.el_asidet {
  height: 100%;
  background: $color-back-nav;
  transition: background-color 0.8s;
  overflow: hidden;
}
.logo {
  height: 78px;
  display: flex;
  justify-content: center;
  align-items: center;
  .them {
    // font-size: 18px;
    color: #fff;
    padding: 10px 0;
    width: 130px;
    .logo_con_text_chs {
      font: bold 18px 'Microsoft YaHei';
      width: 100%;
      white-space: nowrap;
      text-align: center;
    }
    .logo_con_text_eng {
      font: 12px 'Microsoft YaHei';
      white-space: nowrap;
      transform: scale(0.6) translate(-34px, 2px);
      width: 100%;
    }
  }
  .them_logo {
    max-width: 36px;
    height: auto;
  }
}
