@import '../../assets/scss/color';
@import '../../assets/scss/fonts';















































































.tabs {
  color: $color-subtitle-text;
  background-color: $color-back-card;
  margin: 0 20px;
  .el-tabs__nav-wrap {
    &::after {
      background-color: initial;
    }
    &::before {
      background-color: initial;
    }
  }
  .el-tabs__active-bar {
    background-color: $color-main;
  }
  .el-tabs__item {
    color: $color-subtitle-text;
    .tab-name {
      width: 100%;
      display: block;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
  .is-active,
  .el-tabs__item:hover {
    color: $color-main;
  }
}
.tabs-left {
  display: flex;
  .is-active,
  .el-tabs__item:hover {
    background: $color-main-opcity;
  }
}
.el-tabs--top {
  .el-tabs__nav-wrap {
    box-shadow: inset 0 -1px 0 0 #f0f1f5;
  }
}
.el-tabs--right {
  .el-tabs__nav-wrap {
    box-shadow: inset 1px 0 0 0 #f0f1f5;
    .el-tabs__nav {
      margin-left: 0;
    }
  }
  .el-tabs__item {
    .is-right {
      text-align: left；;
    }
  }
}
.el-tabs--left {
  .el-tabs__nav-wrap {
    box-shadow: inset -1px 0 0 0 #f0f1f5;
  }
  .el-tabs__nav {
    margin-left: 0;
  }
  .el-tabs__item {
    &.is-left {
      text-align: left;
    }
  }
}
