@import '../../assets/scss/color';
@import '../../assets/scss/fonts';




















































































































































.hidden-options {
  display: none;
}

/*滚动条整体部分,必须要设置*/
::-webkit-scrollbar {
  width: 10px;
  height: 10px;
  background-color: #fdfdfd;
}
/*滚动条的轨道*/
::-webkit-scrollbar-track {
  background-color: #fdfdfd;
}
/*滚动条的滑块按钮*/
::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #dde3ea;
}
/*滚动条的上下两端的按钮*/
::-webkit-scrollbar-button {
  height: 10px;
  background-color: #fdfdfd;
}
