@import '../../../assets/scss/color';
@import '../../../assets/scss/fonts';



















































































































































































.theme_group {
  display: flex;
  justify-content: space-around;
  .el-radio-button {
    border: 1px solid transparent;
  }
  .el-radio-button__orig-radio:checked + .el-radio-button__inner {
    box-shadow: 0 0 0 0 transparent;
  }
  .el-radio-button--small .el-radio-button__inner {
    border: none;
  }
  .theme_div {
    display: inline-block;
    width: 20px;
    height: 20px;
    box-sizing: border-box;
  }
  .is-active .theme_div {
    &:after {
      content: '';
      border: 6px solid transparent;
      border-bottom-color: $color-icon;
      position: absolute;
      padding: 0;
      top: 28px;
      left: 19px;
    }
  }
  .theme_default {
    background-color: rgba(47, 106, 255, 1);
  }
  .theme_5D9FCB {
    background-color: #5d9fcb;
  }
  .theme_black {
    background-color: #575757;
  }
  .theme_yellow {
    background-color: #9a9c02;
  }
  .theme_green {
    background-color: #0b5001;
  }
  .theme_blue {
    background-color: #2554f0;
  }
}

.themesvg {
  margin-left: 10px;
  width: 16px !important;
  height: 16px !important;
  cursor: pointer;
  color: $color-icon;
  &:hover {
    color: $color-main;
  }
}
