<template>
  <div class="navBar">
    <div class="navBarRow">
      <Collapse></Collapse>
      <Theme class="theme_box"></Theme>
      <p class="userName" @click="userNameClick">
        {{ $store.state.user.userInfo.name }}</p
      >
      <div class="userImg">
        <el-dropdown @command="handleCommand">
          <div class="headImg">
            <el-badge :value="total" :max="99" :hidden="total == 0">
              <img src="../../assets/images/yummi.jpg" alt="" class="avatar" />
            </el-badge>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="editPassword">修改密码</el-dropdown-item>
            <el-dropdown-item command="unreadMsg">未读消息</el-dropdown-item>
            <el-dropdown-item command="logout">注销</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
    <el-dialog
      v-dialogDrag
      class="baseDialog"
      title="修改密码"
      :visible.sync="editPwdVisible"
      width="500px"
    >
      <el-form
        ref="form"
        size="small"
        :model="form"
        :rules="formRules"
        class="form"
        label-width="110px"
      >
        <el-form-item label="原密码：" prop="oldPwd">
          <el-input v-model="form.oldPwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="新密码：" prop="newPwd">
          <el-input v-model="form.newPwd" type="password"></el-input>
        </el-form-item>
        <el-form-item label="确认新密码：" prop="confirmPassword">
          <el-input v-model="form.confirmPassword" type="password"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="editPwdVisible = false">取消</el-button>
        <el-button type="primary" @click="submitCopyForm('form')"
          >确定</el-button
        >
      </div>
    </el-dialog>
    <simple-dialog
      class="dialogClass"
      dialogTitle=""
      :dialog-visible="unreadMsgVisible"
      @cancelEmit="cancelDialog"
      dialogWidth="70%"
    >
      <base-table-panel
        title="未读消息列表"
        class="sortTable noTableBorder"
        :data-source="mailListArr"
        :columns="columns"
        :rightBtns="operationsData"
        @selection-change="handleSelectionChange"
        has-pagination
        :total="total"
        :current-page="page"
        :page-size="limit"
        @handleCurrent="handleCurrent"
        @handleSize="handleSize"
      >
      </base-table-panel>
    </simple-dialog>
  </div>
</template>
<script>
import color from '@assets/scss/index.scss';
import { editPassword } from '@src/api/user';
import { mapState } from 'vuex';
import Theme from './components/Theme.vue';
import { Notification } from 'element-ui';
import Collapse from './components/Collapse';
import {
  updateOneRead,
  mailQuery,
  updateSomeRead,
  updateAllRead
} from '../../api/user';
export default {
  components: {
    Theme,
    Collapse
  },
  data() {
    const _this = this;
    const checkPassValidator = (rule, value, callback) => {
      if (value === '') {
        callback(new Error('请再次输入密码'));
      } else if (value !== this.form.newPwd) {
        callback(new Error('两次输入密码不一致!'));
      } else {
        callback();
      }
    };
    return {
      isFullScreen: false,
      main: color['global-color-main'],
      navActive: '1',
      editableTabsValue: '1',
      searchInput: false,
      editableTabs: [
        {
          title: '首页',
          name: '1',
          content: 'Tab 1 content',
          id: 1
        }
      ],
      tabIndex: 2,
      state2: '',
      editPwdVisible: false,
      unreadMsgVisible: false,
      form: {
        oldPwd: '',
        newPwd: '',
        confirmPassword: ''
      },
      formRules: {
        oldPwd: [{ required: true, message: '请输入原密码', trigger: 'blur' }],
        newPwd: [{ required: true, message: '请输入新密码', trigger: 'blur' }],
        confirmPassword: [
          { required: true, validator: checkPassValidator, trigger: 'blur' }
        ]
      },
      readIds: [],
      columns: [
        {
          prop: '',
          type: 'selection',
          width: '40',
          align: 'center'
        },
        {
          prop: 'message',
          minWidth: '200',
          label: '消息内容',
          'show-overflow-tooltip': true,
          align: 'center'
        },
        {
          prop: 'sendeTime',
          minWidth: '100',
          label: '发送时间',
          'show-overflow-tooltip': true,
          align: 'center'
        },
        {
          prop: '',
          align: 'center',
          label: '操作',
          'min-width': '60',
          headerAlign: 'center',
          actions: [
            {
              code: 'markRead',
              text: '标为已读',
              onClick: _this.oneMarkRead
            }
          ]
        }
      ],
      mailListArr: [],
      page: 1,
      limit: 10,
      total: 0,
      operationsData: [
        {
          title: '标记已读',
          code: 'markRead',
          iconClass: '',
          onClick: this.someMarkRead
        },
        {
          title: '全部已读',
          code: 'allMarkRead',
          iconClass: '',
          onClick: this.allMarkRead
        }
      ]
    };
  },
  computed: {
    ...mapState({
      mailList: (state) => state.user.mailList,
      usercode: (state) => state.user.userInfo.usercode
    }),
    avatar() {
      return this.$store.state.personal.avatar;
      // return '';
    }
  },
  mounted() {
    // if (!this.$store.state.personal.avatar) {
    //   this.$store.dispatch('personal/getAvatar');
    // }
    // window.onresize = () => {
    //   if (!this.checkFull()) {
    //     this.isFullScreen = false;
    //   }
    // };
  },
  methods: {
    someMarkRead() {
      updateSomeRead(this.readIds).then(({ data, error }) => {
        if (!error) {
          this.getMailList();
        } else {
          this.$message.error(error.message);
        }
      });
    },
    allMarkRead() {
      updateAllRead().then(({ data, error }) => {
        if (!error) {
          this.getMailList();
        } else {
          this.$message.error(error.message);
        }
      });
    },
    getMailList() {
      const obj = {
        page: this.page,
        limit: this.limit,
        receiveId: this.usercode
      };
      mailQuery(obj).then(({ data, error }) => {
        if (!error) {
          this.mailListArr = data.rows;
          this.total = data.total;
          this.$store.commit('SET_MAIL_LIST', data.rows);
        } else {
          this.$message.error(error.message);
        }
      });
    },
    handleSize(val) {
      this.limit = val;
      this.readIds.length ? this.someMarkRead() : this.getMailList();
    },
    handleCurrent(val) {
      this.page = val;
      this.readIds.length ? this.someMarkRead() : this.getMailList();
    },
    oneMarkRead(record) {
      updateOneRead(record.id).then(({ data, error }) => {
        if (!error) {
          this.getMailList();
        } else {
          this.$message.error(error.message);
        }
      });
    },
    handleSelectionChange(val) {
      this.readIds = [];
      val.forEach((item) => {
        this.readIds.push(item.id);
      });
    },
    cancelDialog() {
      this.unreadMsgVisible = !this.unreadMsgVisible;
    },
    checkFull() {
      let isFull =
        window.fullScreen ||
        document.webkitIsFullScreen ||
        document.msFullscreenEnabled;
      if (isFull === undefined) isFull = false;
      return isFull;
    },
    submitCopyForm(formName) {
      this.$refs[formName].validate((valid) => {
        if (valid) {
          const params = {
            oldPassword: this.form.oldPwd,
            newPassword: this.form.newPwd
          };
          editPassword(params).then(({ data, error }) => {
            if (!error) {
              // 添加判断，data是否为null，如果不为null，表示出错
              if (data) {
                this.$message.error(data);
                return false;
              }
              this.$message.success('密码修改成功');
              this.editPwdVisible = false;
              setTimeout(() => {
                const _self = this;
                this.$store.dispatch('logOut', _self).then();
              }, 1000);
            } else {
              this.$message.error(error.message);
            }
          });
        } else {
          return false;
        }
      });
    },
    handleCommand(command) {
      switch (command) {
        case 'logout':
          this.logout();
          break;
        case 'editPassword':
          this.editPwdVisible = true;
          break;
        case 'unreadMsg':
          this.unreadMsgVisible = true;
          this.getMailList();
          break;
      }
    },
    logout() {
      Notification.closeAll();
      const _self = this;
      this.$store.dispatch('logOut', _self);
    },
    userNameClick() {
      this.$router.push('/home/index');
    }
  }
};
</script>
<style lang="scss">
.navBar {
  .dialogClass {
    .el-dialog__header {
      padding: 20px 20px 10px;
    }
  }
}
</style>
<style lang="scss" scoped>
.navBar {
  .navBarRow {
    height: 46px;
    line-height: 46px;
    .theme_box {
      display: inline-block;
    }
  }
  .form {
    margin: 0 50px 0 35px;
  }
  .NavBar {
    float: left;
  }
  .userImg {
    float: right;
    padding-top: 10px;
    line-height: 30px;
    .headImg {
      float: left;
      width: 25px;
      height: 25px;
      background: $color-table-header-bg;
      border-radius: 50%;
    }
    .avatar {
      width: 25px;
      height: 25px;
      border-radius: 50%;
    }
    .fullScreenBtn {
      margin-right: 10px;
      font-size: 14px;
      line-height: 10px;
      color: $color-white;
      cursor: pointer;
    }
  }
  .userName {
    float: right;
    padding-left: 12px;
    margin: 0;
    font-size: 14px;
    color: $color-main-text;
    cursor: pointer;
  }
}
</style>
<style>
.headImg .el-badge .el-badge__content.is-fixed {
  right: 0;
  transform: translateY(-50%) translateX(-70%);
  -webkit-transform: translateY(-50%) translateX(-70%);
}
</style>
