@import '../../assets/scss/color';
@import '../../assets/scss/fonts';



















































.dialogStyle {
  .el-dialog__header {
    padding: 8px 20px;
    background-color: $color-back-header;
    .el-dialog__title {
      color: $color-subtitle-text;
      font-size: 14px;
    }
    .el-dialog__headerbtn {
      top: 10px;
    }
  }
  .el-dialog__body {
    padding: 20px;
  }
  .el-dialog__footer {
    padding: 10px 20px;
    button {
      font-size: 14px;
      padding: 6px 12px;
    }
    .el-button--primary {
      background-color: #3674f9;
      border-color: #3674f9;
    }
  }
}
