@import '../../assets/scss/color';
@import '../../assets/scss/fonts';






































































































































































































































































































.el-scrollbar__view {
  .tags-view-container {
    .tags-view-item {
      height: 36px;
      .title {
        font-size: 12px;
      }
    }
  }
}
.basicQuery {
  .smallSelect {
    width: 24%;
    min-width: 140px;
    max-width: 240px;
  }
  .el-button--primary {
    color: #fff;
    background-color: $color-main;
    border-color: $color-main;
  }
  .selectTitle {
    font-size: 14px;
    color: $color-mainfont;
  }
  .pageTitle {
    font-size: 16px;
    font-weight: bold;
    color: $color-mainfont;
  }
  .smallSelect + .smallSelect {
    margin-left: 10px;
  }
  .pageTitle + .basicQueryTitle,
  .smallSelect + .basicQueryTitle,
  .basicQueryBtn + .basicQueryTitle,
  .smallSelect + .basicQueryBtn {
    margin-left: 20px;
  }
  .smallSelect + .selectTitle {
    margin-left: 40px;
  }

  /* 查询条件里，条件间间距为40px，采用form表单布局，去掉每个item10px的右margin */
  .formItemtTitle {
    margin-right: 0;
  }
  .formItemtTitle + .formItemtTitle {
    margin-left: 40px;
  }

  /* 用form表单布局的，将不再识别 smallSelect + .basicQueryBtn的样式 ，所以为查询按钮加以下类名 */
  .authorizeBtn {
    margin-left: 20px;
  }

  /* 查询条件和高级查询里的输入框或查询框宽度为224px */
  .filterWidth {
    width: 224px;
  }

  /* 修改结束 */
  .right {
    text-align: right;
    .iconBtn {
      padding: 10px;
      font-size: 14px;
    }
  }

  .operate-left-form {
    background-color: $color-back-header;
    transition: background-color 0.8s;
    border: 1px solid $color-table-cutting;
    padding: 20px 20px 10px 20px;

    .el-form-item {
      margin-bottom: 10px;
    }
  }
}
.transitionBox {
  position: relative;
  padding: 10px 10px 0 10px;
  margin: 8px 0 0 0;
  border: 1px solid $color-main;

  /* <!--&::after {--> */

  /* <!--position: absolute;--> */

  /* <!--top: -7px;--> */

  /* <!--left: 26px;--> */

  /* <!--display: block;--> */

  /* <!--width: 12px;--> */

  /* <!--height: 12px;--> */

  /* <!--content: '';--> */

  /* <!--background-color: $color-white;--> */

  /* <!--border: 1px solid $color-main;--> */

  /* <!--border-right: none;--> */

  /* <!--border-bottom: none;--> */

  /* <!--transform: rotate(45deg);--> */

  /* <!--}--> */
  .grid {
    .box {
      float: left;
      width: (100% / 5);
      padding-right: 10px;
      .el-form-item {
        position: relative;
        margin-bottom: 10px;
      }
    }
    // 报表管理 高级筛选中   截止时间宽度为上边两个搜索条件宽度的二倍
    .box1 {
      float: left;
      width: (200% / 5);
      padding-right: 10px;
      .el-form-item {
        position: relative;
        margin-bottom: 10px;
      }
    }
    .longData .el-tag:first-child {
      max-width: calc(100% - 72px);
      .el-select__tags-text {
        display: inline-block;
        width: calc(100% - 18px);
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
      .el-icon-close {
        top: -7px;
      }
    }
    .cutOut .el-select__tags .el-tag:first-child {
      max-width: 120px;
      .el-select__tags-text {
        display: inline-flex;
        width: 86px;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .specialBox .el-form-item {
      margin-bottom: 20px;
    }
    .boxBtn1 {
      .el-form-item__content {
        margin-left: 30px !important;
      }
    }
    .boxBtn2 {
      float: right;
      // width: 20%;
      padding-right: 10px;
      text-align: right;
      .el-form-item__content {
        margin-left: 0 !important;
      }
    }
    .boxBtn3 {
      float: left;
      width: 100%;
      text-align: center;
      .el-form-item__content {
        margin-left: 0 !important;
      }
    }
    &::after {
      display: block;
      clear: both;
      visibility: hidden;
      content: '';
    }
  }
  .el-select,
  .el-date-editor {
    width: 100%;
  }
  .el-form-item__label {
    position: absolute;
    top: 50%;
    line-height: 20px;
    transform: translate(0%, -50%);
  }
}
.tips-bar {
  margin-top: 10px;
  font-size: 14px;
  color: $color-mainfont;
  .redTips {
    border: 1px solid $color-status-danger;
    border-radius: 2px;
  }
  .tips-bar-header {
    height: 38px;
    padding: 0 10px;
    line-height: 38px;
    background-color: rgba($color-status-danger, 0.1);
    .redTipsIcon {
      color: $color-status-danger;
    }
    .redTipsRight {
      text-align: right;
      .openIcon {
        padding-left: 4px;
      }
    }
  }
  .tips-bar-body {
    max-height: 242px;
    padding: 0 10px;
    overflow-y: auto;
    line-height: 40px;
    border-top: 1px solid $color-status-danger;
    .bodyTitle {
      font-weight: bold;
    }
    .bodyBox + .bodyBox {
      border-top: 1px solid $color-line-button;
    }
  }
}
