@import 'color';
@import 'fonts';

.CodeMirror-gutters {
  border-right: 1px solid $color-table-cutting !important;
  background-color: $color-back-header !important;
}
.el-table--enable-row-hover .el-table__body tr:hover>td {
  background-color: $color-form-hover;
}

// 单选、多选按钮 下拉菜单样式
.el-select-dropdown__item.selected,
.el-radio__input.is-checked+.el-radio__label,
.el-checkbox__input.is-checked+.el-checkbox__label {
  color: $color-main;
}
.el-select .el-input.is-focus .el-input__inner,
.el-input.is-active .el-input__inner,
.el-textarea__inner:focus, .el-input__inner:focus,
.el-select .el-input__inner:focus,
.el-checkbox__inner:hover {
  border-color: $color-main;
}

.el-checkbox__input.is-checked .el-checkbox__inner,
.el-checkbox__input.is-indeterminate .el-checkbox__inner,
.el-radio__input.is-checked .el-radio__inner {
  background-color: $color-main;
  border-color: $color-main;
}
.el-select-dropdown__item.hover,
.el-select-dropdown__item:hover {
  background-color: $color-form-hover;
}
.el-date-table td.today span,
.el-date-table td.available:hover {
  color: $color-main;
}
.el-date-picker__header-label.active,
.el-date-picker__header-label:hover {
  color: $color-main;
}
.el-date-table td.current:not(.disabled) span {
  background-color: $color-main;
}
.el-picker-panel__icon-btn:hover,
.el-time-spinner__item.active:not(.disabled),
.el-time-panel__btn.confirm {
  color: $color-main;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected {
  color: $color-main;
  background-color: $color-back-card;
}
.el-select-dropdown.is-multiple .el-select-dropdown__item.selected.hover {
  background-color: $color-form-hover;
}
.el-input__inner {
  border: 1px solid $color-control;
}
.el-button:not(.el-button--text):not(.login-button):not(.el-button--success):not(.icon_button):not(.el-button--warning):not(.el-button--danger):not(.el-button--info) {
  color: $color-main;
  border-color: $color-control;
  background-color: transparent;
  &:focus {
    color: $color-main;
    border-color: $color-control;
    background-color: transparent;
  }
  &:hover {
    color: $color-main;
    border-color: $color-btn-hover;
    background-color: transparent;
  }
  &:active {
    color: $color-main;
    border-color: $color-main;
    background-color: transparent;
  }
}

.el-radio-button__orig-radio:checked+.el-radio-button__inner {
  color: $color-main;
  background-color: $color-back-card;
  border-color: $color-main;
  box-shadow: -1px 0 0 0 $color-main;
}
.el-radio-button__inner:hover {
  color: $color-main;
}
.el-tabs__item.is-active,
.el-tabs__item:hover {
  color: $color-main;
}
.el-tabs__active-bar {
  background-color: $color-main;
}
.el-button--mini {
  padding: 7px 15px !important;
}
.el-dropdown-menu__item:focus, .el-dropdown-menu__item:not(.is-disabled):hover {
  background-color: $color-main-opcity;
  color: $color-main;
}
.el-loading-spinner .path {
  stroke: $color-main;
}
.el-checkbox__input.is-focus .el-checkbox__inner {
  border-color: $color-main;
}
.el-dialog__headerbtn:focus .el-dialog__close, .el-dialog__headerbtn:hover .el-dialog__close {
  color: $color-main;
}
.el-pagination__sizes .el-input .el-input__inner:hover {
  border-color: $color-main;
}

.el-cascader .el-input .el-input__inner:focus, .el-cascader .el-input.is-focus .el-input__inner {
  border-color: $color-main;
}
.el-button--primary:not(.is-plain) {
  color: #fff !important;
  background-color: $color-main !important;
  border-color: $color-main !important;
  &:focus {
    color: #fff !important;
    background-color: $color-main !important;
    border-color: $color-main !important;
  }
  &:hover {
    color: #fff !important;
    background-color: $color-btn-hover !important;
    border-color: $color-btn-hover !important;
  }
  &:active {
    color: #fff !important;
    background-color: $color-btn-click !important;
    border-color: $color-btn-click !important;
  }
}
.el-button--primary.is-disabled, .el-button--primary.is-disabled:focus, .el-button--primary.is-disabled:hover {
  color: #fff !important;
  background-color: $color-Disable !important;
  border-color: $color-Disable !important;
}
.el-radio__inner:hover {
  border-color: $color-main;
}
.el-cascader-node.in-active-path, .el-cascader-node.is-active, .el-cascader-node.is-selectable.in-checked-path {
  color: $color-main;
}
// switch开关
.el-switch.is-checked .el-switch__core {
  border-color: $color-main;
  background-color: $color-main;
}
.el-switch__core {
  border: 1px solid $color-icon;
  background: $color-icon;
}
.el-tag {
  background-color: $color-back-card;
  border-color: $color-main;
  color: $color-main;
  
}
.el-button.icon_button {
  border: 1px solid $color-control;
  color: $color-main-text;
  background-color: transparent;
  .svg-icon {
    color: $color-icon;
  }
  &:focus {
    color: $color-main-text;
    border-color: $color-control;
    background-color: transparent;
    .svg-icon {
      color: $color-icon;
    }
  }
  &:hover {
    color: $color-main;
    border-color: $color-main;
    background-color: transparent;
    .svg-icon {
      color: $color-main;
    }
  }
  &:active {
    color: $color-btn-click;
    border-color: $color-btn-click;
    background-color: transparent;
    .svg-icon {
      color: $color-btn-click;
    }
  }
  
}
.el-button.icon_button.is-disabled, .el-button.icon_button.is-disabled:focus, .el-button.icon_button.is-disabled:hover {
  color: $color-textfailure;
  border-color: $color-control;
  background-color: $color-back-header;
  .svg-icon {
    color: $color-textfailure;
  }
}
.el-message .el-icon-success {
  color: $color-Success;
}
.el-message--success .el-message__content {
  color: $color-subtitle-text;
}
.el-message .el-icon-warning {
  color: $color-Warning;
}
.el-message--warning .el-message__content {
  color: $color-subtitle-text;
}
.el-message .el-icon-error {
  color: $color-Error;
}
.el-message--error .el-message__content {
  color: $color-subtitle-text;
}
.el-message .el-icon-info {
  color: $color-Silent;
}
.el-message--info .el-message__content {
  color: $color-subtitle-text;
}
.wh_content_item .wh_isToday[data-v-72edc50b] {
  background: $color-main !important;
}
.el-button--text {
  color: $color-link-text;
  &:focus {
    color: $color-link-text;
  }
  &:hover {
    color: $color-btn-hover;
  }
  &:active {
    color: $color-btn-click;
  }
}
.el-button--text.is-disabled, .el-button--text.is-disabled:focus, .el-button--text.is-disabled:hover {
  color: $color-Disable;
}
.el-button--success {
  color: #FFF;
  background-color: $color-Success;
  border-color: $color-Success;
  &:focus {
    background-color: $color-Success;
    border-color: $color-Success;
  }
  &:hover {
    background-color: $color-Success-hover;
    border-color: $color-Success-hover;
  }
  &:active {
    background-color: $color-Success-click;
    border-color: $color-Success-click;
  }
}
.el-button--success.is-disabled, .el-button--success.is-disabled:focus, .el-button--success.is-disabled:hover {
  background-color: $color-Success-disable;
    border-color: $color-Success-disable;
}

.el-button--warning {
  color: #FFF;
  background-color: $color-Warning;
  border-color: $color-Warning;
  &:focus {
    background-color: $color-Warning;
    border-color: $color-Warning;
  }
  &:hover {
    background-color: $color-Warning-hover;
    border-color: $color-Warning-hover;
  }
  &:active {
    background-color: $color-Warning-click;
    border-color: $color-Warning-click;
  }
}
.el-button--warning.is-disabled, .el-button--warning.is-disabled:focus, .el-button--warning.is-disabled:hover {
  background-color: $color-Warning-disable;
    border-color: $color-Warning-disable;
}

.el-button--danger {
  color: #FFF;
  background-color: $color-Error;
  border-color: $color-Error;
  &:focus {
    background-color: $color-Error;
    border-color: $color-Error;
  }
  &:hover {
    background-color: $color-Error-hover;
    border-color: $color-Error-hover;
  }
  &:active {
    background-color: $color-Error-click;
    border-color: $color-Error-click;
  }
}
.el-button--danger.is-disabled, .el-button--danger.is-disabled:focus, .el-button--danger.is-disabled:hover {
  background-color: $color-Error-disable;
    border-color: $color-Error-disable;
}
.el-form-item__error {
  color: $color-Error;
}
.el-form-item.is-error .el-input__inner, .el-form-item.is-error .el-input__inner:focus, .el-form-item.is-error .el-textarea__inner, .el-form-item.is-error .el-textarea__inner:focus, .el-message-box__input input.invalid, .el-message-box__input input.invalid:focus {
  border-color: $color-Error;
}
.el-form-item.is-required:not(.is-no-asterisk) .el-form-item__label-wrap>.el-form-item__label:before, .el-form-item.is-required:not(.is-no-asterisk)>.el-form-item__label:before {
  color: $color-Error;
}
.el-message-box__status.el-icon-warning {
  color: $color-Warning;
}
.el-badge__content {
  background-color: $color-Error;
}
.el-badge__content--warning {
  background-color: $color-Warning;
}
.el-badge__content--primary {
  background-color: $color-main;
}
.themeIcon {
  color: $color-link-text;
}
.el-form-item__label {
  color: $color-main-text;
}
.el-pagination__jump, .el-pagination__total, .el-pagination__sizes, .el-tree, .el-textarea__inner, .el-radio, .el-input__inner, .el-select-dropdown__item {
  color: $color-main-text;
  background-color: $color-back-card
}
.el-pagination .btn-next, .el-pagination .btn-prev {
  color: $color-main-text;
  background-color: $color-back-card
}
.el-dialog, .el-pager li {
  background-color: $color-back-card
}
.el-menu-item {
  color: $color-subtitle-text;
}
.el-tabs__item {
  color: $color-subtitle-text;
}
.el-tabs__nav-wrap::after {
  background-color: $color-table-cutting;
}
.el-radio-button__inner {
  color: $color-subtitle-text;
  background: $color-back-card;
  border: 1px solid $color-control;
}
.el-checkbox {
  color: $color-main-text;
}
.el-table__empty-text {
  color: $color-textassist;
}
.el-table, .el-table__expanded-cell {
  background-color: $color-back-card;
  transition: background-color .8s;
}
.el-pagination button:disabled {
  color: $color-Silent;
  background-color: $color-back-card;
}
.el-select-dropdown {
  border: 1px solid $color-control;
  background-color: $color-back-card;
}
.el-textarea__inner {
  border: 1px solid $color-control;
}
.el-popover {
  background-color: $color-back-card;
  border: 1px solid $color-control;
}


.el-popper[x-placement^=top] .popper__arrow::after {
  border-top-color: $color-back-card;
}
.el-popper[x-placement^=right] .popper__arrow::after {
  border-right-color: $color-back-card;
}
.el-popper[x-placement^=bottom] .popper__arrow::after {
  border-bottom-color: $color-back-card;
}
.el-tree-node__content:hover {
  background-color: $color-form-hover;
}
.el-tree-node:focus>.el-tree-node__content {
  background-color: $color-form-hover;
}
.el-input.is-disabled .el-input__inner {
  background-color: $color-form-hover;
  border-color: $color-control;
  color: $color-textfailure;
}
.el-loading-mask {
  background-color: $color-back-card;
}
.el-dropdown-menu {
  background-color: $color-back-card;
  border: 1px solid $color-control;
  box-shadow: 0 2px 12px 0 $color-control;
}
.el-dropdown-menu__item {
  color: $color-main-text;
}
.el-message-box {
    border: 1px solid $color-control;
    background-color: $color-back-card;
}
.el-message-box__title {
  color: $color-subtitle-text;
}
.el-message-box__content {
  color: $color-main-text;
}
.el-autocomplete-suggestion {
  border: 1px solid $color-control;
  background-color: $color-back-card;
}
.el-autocomplete-suggestion li {
  color: $color-main-text;
}
.el-autocomplete-suggestion li.highlighted, .el-autocomplete-suggestion li:hover {
  background-color: $color-form-hover;
}
.el-message--error {
  background-color: $color-Error-disable;
  border-color: $color-Error;
}
.el-message--warning {
  background-color: $color-Warning-disable;
  border-color: $color-Warning;
}
.el-message--success {
  background-color: $color-Success-disable;
  border-color: $color-Success;
}
.color_main_text {
  color: $color-main-text;
}
.el-form-item__content {
  color: $color-main-text;
}
.el-switch__label.is-active {
  color: $color-main;
}
.el-switch__label {
  color: $color-textassist;
}
.el-picker-panel {
  color: $color-main-text;
  border: 1px solid $color-control;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
  background: $color-back-header;
}
.time-select-item.selected:not(.disabled) {
  color: $color-main;
}
.time-select-item:hover {
  background-color: $color-form-hover;
}
.el-cascader__dropdown {
  margin: 5px 0;
  font-size: 14px;
  background: $color-back-card;
  border: 1px solid $color-control;
  border-radius: 4px;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
.el-cascader-node:not(.is-disabled):focus, .el-cascader-node:not(.is-disabled):hover {
  background: $color-form-hover;
}
.el-radio {
  background: transparent;
}
.el-radio__input.is-disabled .el-radio__inner, .el-radio__input.is-disabled.is-checked .el-radio__inner {
  background-color: $color-form-hover;
  border-color: $color-control;
}
.el-cascader-menu {
  color: $color-main-text;
  border-right: solid 1px $color-control;
}
// .el-drawer {
//   background-color: $color-back-header;
// }
// .el-drawer__header {
//   color: $color-subtitle-text;
// }
.el-tag.el-tag--info {
  background-color: $color-back-header;
  border-color: $color-control;
  color: $color-main-text;
}
.el-date-editor .el-range-input {
  color: $color-main-text;
  background-color: $color-back-header;
}
.el-date-editor .el-range-separator {
  color: $color-main-text;
}
.el-time-spinner__item {
  color: $color-main-text;
}
.el-time-panel {
  border: 1px solid $color-control;
  background-color: $color-back-card;
  box-shadow: 0 2px 12px 0 rgba(0,0,0,.1);
}
.el-time-spinner__item:hover:not(.disabled):not(.active) {
  background: $color-form-hover;
}
.el-time-panel__btn {
  color: $color-main-text;;
}