<template>
  <svg :class="svgClass" aria-hidden="true" @click="svgClick">
    <use :xlink:href="iconName" />
  </svg>
</template>

<script>
export default {
  name: 'SvgIcon',
  props: {
    iconClass: {
      type: String,
      required: true
    },
    className: {
      type: String,
      default: ''
    }
  },
  computed: {
    iconName() {
      return `#icon-${this.iconClass}`;
    },
    svgClass() {
      if (this.className) {
        return 'svg-icon ' + this.className;
      } else {
        return 'svg-icon';
      }
    }
  },
  methods: {
    svgClick() {
      this.$emit('svgClick');
    }
  }
};
</script>

<style scoped>
.svg-icon {
  width: 12px;
  height: 12px;
  overflow: hidden;
  vertical-align: -0.15em;
  fill: currentColor;
}
</style>
